<template>
	<div class="star" :class="starType">
		<span class="star-item" :class="itemClass" v-for="itemClass in itemClasses"></span>
	</div>
</template>	
<script>
	export default{
		props:{
			size:{
				type:Number,
			},
			score:{
				type:Number,
			}
		},
		computed:{
			starType(){
				return 'star_'+this.size;
			},
			itemClasses(){
				let result=[];
				let score=Math.floor(this.score*2)/2;
				let hasDecimal=score%1!==0;
				let integer=Math.floor(score);
				for(let i=0;i<integer;i++){
					result.push("on")
				}
				if(hasDecimal){
					result.push("half")
				}
				while(result.length<5){
					result.push("off")
				}
				return result 
			}
		}
	}
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import '../../common/style/mixin.styl'
  .star
    font-size:0
    .star-item
      display:inline-block
      background-repeat:no-repeat
    &.star_48      // &表示同级
      .star-item   //star_48里面的.star-item
        width:20px
        height:20px
        margin-right:22px
        background-size:20px 20px
        &:last-child
          margin-right:0
        &.on
          bg-image('star48_on')
        &.half
          bg-image('star48_half')
        &.off
          bg-image('star48_off')
    &.star_36
      .star-item
        width:15px
        height:15px
        margin-right:6px
        background-size:15px 15px
        &:last-child
          margin-right:0
        &.on
          bg-image('star36_on')
        &.half
          bg-image('star36_half')
        &.off
          bg-image('star36_off')
    &.star_24
      .star-item
        width:10px
        height:10px
        margin-right:3px
        background-size:10px 10px
        &:last-child
          margin-right:0
        &.on
          bg-image('star24_on')
        &.half
          bg-image('star24_half')
        &.off
          bg-image('star24_off')
</style>


